"use strict";
App.controller('flichtController', ['$scope', '$http', '$window', '$state', function($scope, $http, $window, $state) {
    let UUID    = $state.router.globals.params.uuid;

    var VALUES = [{data:UUID}];
    $http.post('./includes/callback.php', VALUES,  {params:{action:"getFlight"}}).then(function(data){
        $scope.flight = data.data;        
    });

    
}]);
