App.config(['$stateProvider', '$urlMatcherFactoryProvider', '$urlRouterProvider', '$locationProvider', '$compileProvider', function($stateProvider, $urlMatcherFactoryProvider, $urlRouterProvider, $locationProvider, $compileProvider){
    $compileProvider.debugInfoEnabled(false);
    $urlRouterProvider.otherwise("/home/");
    $urlMatcherFactoryProvider.caseInsensitive(true);
    
    $stateProvider
        .state({
            name:"home",
            url: "/home/",
            views : {
                "mainpage" : {
                    templateUrl : "./html/home.html?v="+controler.version,
                    controller  : "HomeController",
                }
            }
        }) 
        .state({
            name:"flicht",
            url: "/flicht/:uuid",
            views : {
                "mainpage" : {
                    templateUrl : "./html/flicht.html?v="+controler.version,
                    controller  : "flichtController",
                }
            }
        })        
}]);
