"use strict";
App.controller('HomeController', ['$scope', '$http', '$window', function($scope, $http, $window) {
      
    reloadBlackboxData();
    getBlackboxData();
    setInterval(function(){
        reloadBlackboxData();       
        getBlackboxData();        
    }, 3000);


    $scope.remove = function(data){
        console.log(data.uuid);
    }

    $scope.view   = function(data){
        console.log(data.uuid);
    }

    function getBlackboxData(){
        $http.get('./includes/callback.php?action=getBlackboxData').then(function(data){
            if(data.status === 200){
                $scope.flichts = data.data;                
            }
        }); 
    }

    function reloadBlackboxData(){
        $http.get('./includes/callback.php?action=test').then(function(data){
            console.log(data.data);
        }); 

        $http.get('./includes/callback.php?action=reloadBlackboxData').then(function(data){
            console.log("get the flicht data after you landed");
        }); 
    }


}]);
